@import 'variable.scss';

.switchContainer {
    margin-top: 2rem;
    width: max-content;
    margin: 2rem auto 0 auto;

    span:nth-child(2) {
        font-weight: bold;
        color: $mint;
        cursor: pointer;

        &:hover {
            text-decoration: underline;
        }
    }
}